import {
	mapState,
	mapActions,
	mapMutations
} from 'vuex'
import commongood from '../../components/common/commongood.vue'
export default {
	name: "",
	components: {
		commongood
	},
	props: {},
	data() {
		return {
			// scroll-view参数
			nameId: null,
			// 用于接收从首页跳过来时接收到的id
			currentId: null
		}
	},
	methods: {
		...mapActions(['getBigClassfy', 'getSmallGoods']),
		// 点击导航栏小类触发的方法
		changeSmall(sid) {
			this.currentId = sid
			this.getSmallGoods(this.currentId)
		}
	},
	mounted() {
		this.getBigClassfy()
		if (this.currentId !== null) {
			this.getSmallGoods(this.currentId)
		}
	},
	onLoad(option) {
		this.currentId = option.id
		this.nameId = "L2" + option.id
	},
	filters: {

	},
	computed: {
		...mapState(['bigClassfy', 'smallObj']),
	},
	watch: {

	},
	directives: {

	}
}
